<script>
export default {
    data() {
        return {
            checked: false,
            settleAccounts: JSON.parse(localStorage.getItem('settleAccounts'))
        }
    },
    computed: {
        allnums: function () {
            return this.settleAccounts.map(o => o.nums).reduce((total, currentValue) => Number(total) + Number(currentValue));
        },
        allprice: function () {
            return this.settleAccounts
                .filter((o) => o.isSelected === true)
                .map((o) => o.price * o.nums)
                .reduce((total, currentValue) => Number(total) + Number(currentValue));
        }
    },

    methods: {
        onClickLeft() {
            this.$router.back()
        },

